.mask-transparent{position:fixed;width:100%;height:100%;top:0;left:0;z-index:5001;background:rgba(40,40,40,0.35)}.toast{position:fixed;min-width:4.5rem;top:50%;left:50%;margin-top:-.6rem;-webkit-transform:translateX(-50%);transform:translateX(-50%);background:rgba(40,40,40,0.75);text-align:center;border-radius:0.06666666666666667rem;color:#fff;z-index:50000}.fade-transition{opacity:1;-webkit-transition:opacity .2s linear;transition:opacity .2s linear}.toast-icon{margin:0.29333333333333333rem 0 0;display:block}.toast-content{margin:0.2rem}.toast-text .toast-content{margin:0;padding:0.13333333333333333rem;border-radius:0.2rem}



